/* .press-start-2p-regular {
  font-family: "Press Start 2P", system-ui;
  font-weight: 400;
  font-style: normal;
} */

/* button-font */
@font-face {
  font-family: 'pixelButtonFont'; /* Replace with your preferred font name */
  src: url(${___CSS_LOADER_URL_REPLACEMENT_0___}) format('truetype');
  /* Add additional src declarations for other font formats if needed */
}

@font-face {
  font-family: 'PirateFont'; /* Replace with your preferred font name */
  src: url(${___CSS_LOADER_URL_REPLACEMENT_1___}) format('truetype');
  /* Add additional src declarations for other font formats if needed */
}

@font-face {
  font-family: 'PirateFontButton'; /* Replace with your preferred font name */
  src: url(${___CSS_LOADER_URL_REPLACEMENT_2___}) format('opentype');
  /* Add additional src declarations for other font formats if needed */
}

body {
  font-family: 'Press Start 2P', Arial, sans-serif;
  margin: 0;
}
.screen {
  height: 100vh;
  width: 100%;
}
.game{
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.board-container {
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.board {
  display: flex;
  flex-wrap: wrap;
  height: 660px;
  width: 647px;
  border: 5px solid #002c66;
  gap: 4px;
  background-color: #002c66;
}

.grid {
  height: 60px;
  width: 60px;
  border: 1px solid #002c66;
  box-sizing: border-box;
  cursor: crosshair;
}

.board button {
  background-color: #2389da;
  /* padding: 10px 10px; */
  padding: 0;
  border: 1px solid #002c66;
  color: whitesmoke;
  /* color: black; */
}

.board.cheat button {
  background-color: green;
}

.inner-board-container {
  text-align: center;
  color: whitesmoke;
  font-weight: bold;
}
.board button:hover {
  /* color: whitesmoke; */
  color: grey;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_3___});
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.board button:active {
  animation: bounce 0.5s;
}

@keyframes bounce {
  0% { transform: scale(1); }
  30% { transform: scale(1.2); }
  50% { transform: scale(0.9); }
  70% { transform: scale(1.1); }
  100% { transform: scale(1); }
}

.game-button-container {
  margin-top: 50px;
  display: flex;
  justify-content: center;
  gap: 50px;
}


.game-button-container button::after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  transition: all 0.5s ease-in-out;
  background-color: #333;
  border-radius: 30px;
  visibility: hidden;
  height: 10px;
  width: 10px;
  z-index: -1;
}

.game-button-container button {
  font-family: 'pixelButtonFont', Arial, sans-serif;
  font-size: 20px;
  height: 50px;
  width: 200px;
  position: relative;
  background-color: transparent;
  cursor: pointer;
  border: 2px solid whitesmoke;
  overflow: hidden;
  border-radius: 30px;
  color: whitesmoke;
  transition: all 0.2s ease-in-out;
}

.game-button-container button:hover {
  box-shadow: 1px 1px 200px #252525;
  color: #fff;
  border: none;
}

.game-button-container button:hover::after {
  visibility: visible;
  transform: scale(100) translateX(2px);
}
